.section-header-main {
  overflow: hidden;
  margin-bottom: 110px;

  .grid-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 570px;

    .title {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      font-family: 'Druk Wide';
      font-weight: 400;
      font-size: 140px;
      line-height: 86%;
      margin-top: -12px;
      display: flex;
      align-items: center;
      color: rgba(249, 249, 249, 0.9);
      opacity: 0.9;
      white-space: nowrap;
    }

    .logo {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      margin-top: -6px;
      z-index: 2;
    }

    .info-block {
      margin-top: auto;
      padding-bottom: 10px;

      .date {
        font-family: 'Neue Plak';
        font-weight: 400;
        font-size: 16px;
        line-height: 117%;
        color: #6D6D6D;
        margin-bottom: 28px;
      }

      .text {
        font-family: 'Druk Wide';
        font-weight: 400;
        font-size: 22px;
        line-height: 92%;
        display: flex;
        align-items: flex-end;
        color: #6D6D6D;
        max-width: 190px;
      }
    }
  }

  @media (max-width: 1200px) {
    .grid-container {
      height: 490px;

      .title {
        font-size: 115px;
        line-height: 86%;
        margin-top: -32px;
        margin-left: 26px;
      }

      .logo {
        width: 404px;
        height: auto;
        margin-top: -24px;
      }

      .info-block {
        margin-left: 26px;
      }
    }
  }

  @media (max-width: 767px) {
    .grid-container {
      align-items: center;
      height: 410px;

      .title {
        font-size: 57px;
        margin-left: 0;
      }

      .logo {
        width: 250px;
      }

      .info-block {
        text-align: center;
        margin-left: 0;
      }
    }
  }
}


